.footer-backdrop {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 100;
    transition: 1s;
    transform: opacity(1);
}
.footer-backdrop.footer-hidden {
    opacity: 0;
    visibility: hidden;
}

.footer-modal {
    
    overflow-x: hidden;
    position: relative;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    max-width: 95%;
    height: 95%;
  
    background-color: $mainBgColor;
    overflow-y: scroll;
}

  
.data-footer-modal-close-button{
  position: absolute;
  top: 10px;
  right: 10px;
  display: flex;
  align-items: center;
  justify-content: center;

  padding: 0;
  margin: 0;
  border: none;
  background-color: transparent;
  cursor: pointer;
  outline: none;
  // z-index: 1;
//   @media screen and (max-width: 767px){
//     top: 15px;
//     right: 20px;
//  }
//  @media screen and (min-width: 768px){
//   top: 15px;
//   right: 25px;
// }
}

.modal__svg-close {
  width: 30px;
  height: 30px;

  &.dark-theme {
    fill: $background-color;
  }
}

  
  
  
.footer-card-list {
   
    justify-content: center;
    padding: 30px;
      @media screen and (max-width: 480px){
       padding: 35px 10px 10px 10px;

    }
    
    &-item {
      transition: 1s;
      transform: scale(1);
      background-color: $background-color;
      border: solid 2px $accentColor;
      border-radius: 5px;
      box-shadow: 0px 2px 1px rgba(0, 0, 0, 0.2), 0px 1px 1px rgba(0, 0, 0, 0.14),
        0px 1px 3px rgba(0, 0, 0, 0.12);
      height: 100%;
      
        &:hover {
          box-shadow: 0 0 20px  $accentColor;
      
      }
    }
      &-img {
        padding: 15px;
        margin: 0 auto;
       
    }
}
.footer-discription  {
    font-family: Roboto;
    font-size: 20px;
    font-weight: 500;
    line-height: 1.17;
    margin-bottom: 10px;
    text-transform: uppercase;
    text-align: center;
    color: $mainTextColor;
}
.footer-proff{
    font-size: 12px;
    font-family: Roboto;
    text-align: center;
    font-weight: 500;
    line-height: 1.17;
    margin-bottom: 10px;
    
   
    color: $modalTitleTextColor;
}
.footer-contact {
    display: block;
    font-family: Roboto;
    font-size: 10px;
    font-weight: 400;
    text-align: center;
    text-decoration: none;
    color: $footerTextColor;
    @media screen and (max-width: 480px){
      font-size: none;
    }
  }
  
  @media screen and (max-width: 767px) {
    .footer-card-list {
      display: block;
      &-item {
        margin: 0 auto;
        max-width: 320px;
  
        &:not(:last-child) {
          margin-bottom: 30px;
        }
      }
}

.footer-name {
      font-family: Roboto;
      font-size: 24px;
      font-weight: 500;
      line-height: 1.17;
      margin-bottom: 10px;
     
      text-align: center;
      color: $footerTextColor;
      text-decoration: underline;
      padding-left: 10px;
      padding-right: 10px;
}
    .footer-proff {
      font-size: 15px;
      line-height: 1.14;
      text-align: center;
    
      margin: 10px 10px 1em 10px;
    }
    .footer-contact {
      font-size: 16px;
    }
  }
  @media screen and (min-width: 768px) {
    .footer-card-list {
      display: flex;
      flex-wrap: wrap;
      &-item {
        max-width: calc((100% - 40px) / 2);
      }
    }
    .footer-name {
      font-family: Roboto;
      font-size: 24px;
      font-weight: 500;
      line-height: 1.17;
      margin-bottom: 10px;
      text-align: center;
      color: $footerTextColor;
      text-decoration: underline;
      padding-left: 10px;
      padding-right: 10px;}

    .footer-contact {
      font-size: 16px;
    }
  }
  @media screen and (min-width: 768px) and (max-width: 1023px) {
    .footer-card-list-item {
      margin-bottom: 30px;
      &:nth-child(odd) {
        margin-right: 30px;
      }
    }
  }
  @media screen and (min-width: 1024px) {
    .footer-card-list {
      flex-wrap: wrap;
      &-item {
        max-width: calc((100% - 30px * 4) / 6);
        margin-bottom: 50px;
        margin-right: 30px;
        &:last-child {
          margin-right: 0px;
        }
        &:nth-child(5) {
          margin-right: 0px;
        }
      }
    }
    .footer-name {
      font-size: 16px;
    }
    .footer-discription {
      font-size: 14px;
    }
    .footer-contact {
      font-size: 10px;
    }
  }

  // mail
  .mail,.git-mail{
    fill: $errorTextColor;
    border-radius: 50px;
    width: 40px;
    height: 40px;
    padding-left: 4px;
    transition: 1s;
    transform: scale(1);
     &:hover,
      &:focus {
      fill: $mainBgColor;
      background-color: $errorTextColor;
    }
  }

  .modal-skills {
    font-family: Roboto;
    font-size: 20px;
    font-weight: 500;
    line-height: 1.17;
    margin-bottom: 10px;
    text-align: center;

    
    color: $modalTitleTextColor;

  }

  .modal-skills_link {
    font-family: Roboto;
    font-size: 15px;
    font-weight: 500;
    line-height: 1.17;
    margin-bottom: 10px;
    text-align: center;

    
    color: $accentColor;

  }


 

.footer-link__mail,
.footer-link__tel {

  font-style: normal;
  font-size: 15px;
  line-height: 1.17;
  color: $footerTextColor;
  margin-bottom: 8px;
  @media screen and (min-width: 1024px){
    font-size: 11px;
    line-height: 1.4;
  }
  
}

.footer-link__mail:hover,
.footer-link__mail:focus,
.footer-link__tel:hover,
.footer-link__tel:focus {
  color: $accentColor;
  transition-duration: 250ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}


.footer-modal_social {
  display: flex;
  justify-content: center;
  
  font-size: 20px;

  color: red;
}


.icon-envelop,
.icon-phone,
.githab,
.linkedin {
  color: $mainBgColor;
  fill: currentColor;
  width: 20px;
  height: 20px;
  margin: 10px 10px 10px 10px;
  @media screen and (min-width: 1024px){
    margin: 5px 5px 5px 5px;
    width: 15px;
    height: 15px;}
  
}
.icon-folder {
  color:$accentColor;
  
  fill: currentColor;
  width: 20px;
  height: 20px;
  margin: 10px 10px 10px 10px;
}

.icon-folder:hover,
.icon-folder:focus
 {
  color:$mainTextColor;
}



.footer-modal-githab__link:hover,
.footer-modal-githab__link:focus,
.footer-link__mail:hover,
.footer-link__mail:focus,
.footer-link__tel:hover,
.footer-link__tel:focus,
.footer-modal-linkedin__link:hover,
.footer-modal-linkedin__link:focus {
  color: $accentColor;
  transition-duration: 250ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}

.collapsible {
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: $background-color;
    color: $accentColor;
    cursor: pointer;
    padding: 10px;
    width: 100%;
    border: none;
    
    outline: none;
    font-size: 15px;
    
}

.active, .collapsible:hover,
.active, .collapsible:focus {
    background-color: $accentColorModal;
    color: $mainTextColor;
}


.content {
    padding: 10px 10px 10px 10px;
    display: none;
    overflow: hidden;
    background-color: $primary-text-color;
}


.cross:after {
  content: '✖';
  left: 6.5px;
  top: 5px;
  
}
 

/*Theme footer*/
footer.light-theme {
  background-color: $secondBgColor;
  color: #333333;
}
footer.dark-theme {
  background-color: #181818;
  color: $mainBgColor;
}
/*Theme footer-modal*/
.footer-modal.light-theme {
  background-color: $secondBgColor;
  color: #333333;
}
.footer-modal.dark-theme {
  background-color: #545454;
  color: $mainBgColor;
}
.modal__svg-close.dark-theme {
  color:$accentColor;  
  fill: currentColor
}

.modal__svg-close:hover,
.modal__svg-close:focus {
  color:$accentColor;  
  fill: currentColor
}